body {
  font-family: "Titillium Web", sans-serif;
  line-height: 1.2;
  font-weight: 400;
  color: #363636;
}
body main h1,
body main h2,
body main h3,
body main h4,
body main h5,
body main h6 {
  font-weight: 400;
  margin: 0;
}
body main h2 {
  font-size: 3rem;
  line-height: 3.45rem;
  margin-bottom: 1rem;
}
@media (min-width: 991px) {
  body main h2 {
    font-size: 3.75rem;
    line-height: 4.313rem;
  }
}
body main h3 {
  font-size: 1.875rem;
  line-height: 2.156rem;
  margin-bottom: 1rem;
}
@media (min-width: 991px) {
  body main h3 {
    font-size: 2.188rem;
    line-height: 2.516rem;
  }
}
body main p {
  font-size: 1.125rem;
  line-height: 1.688rem;
}
@media (min-width: 991px) {
  body main p {
    font-size: 1.375rem;
    line-height: 2.063rem;
  }
}

* {
  position: relative;
}

section {
  display: flex;
  justify-content: center;
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 30px;
  gap: 30px;
  width: 100%;
  padding: 0 24px;
  box-sizing: border-box;
}
@media (min-width: 480px) {
  .grid {
    max-width: 480px;
  }
}
@media (min-width: 768px) {
